export default {
	data() {
		return {
			showOrnot: true,
			title: '在南华',
			list: ['全部', '通知公告', '个人发布'],
			activeIndex: 0,
			pubIndex: [0, 1, 2, 3],
			pubValue: ['发布时间', '评论时间', '喜欢热度', '评论热度'],
			publish: '发布时间',
			searchWord: ''
		}
	},
	methods: {
		searchMain() {
			this.showOrnot = !this.showOrnot;
		},
		hidePicker(res) {
			console.log(this.pubValue[res.detail.value]);
			this.publish = this.pubValue[res.detail.value];
		},
		getActive(index) {
			this.activeIndex = index;
			uni.showToast({
				title: '动态加载中',
				icon: 'loading',
				duration: 1000
			})
		}
	}
}
